@import url(https://fonts.googleapis.com/css2?family=Crimson+Pro:wght@600;700;800;900&family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lora:wght@400;500;700&family=Montserrat:wght@200;400;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
    background: #181719;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 18px;
    padding: 20px 0;
    height: 80px;
}

.navbar-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;

}

.container {
    width: 100%;
    max-width: 1300px;
    margin-left: auto;
    margin-right: auto;
    padding-right: 50px;
    padding-left: 50px;
}

.navbar-logo {
    cursor: pointer;
    text-decoration: none;
}

.navbar-logo h1 {
    color: #fff;
    border: 1px solid #fff;
    font-size: 14px;
    padding: 10px;
    font-family: 'Crimson Pro', serif;
}

.nav-menu {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    list-style: none;
    text-align: center;
    -webkit-justify-content: end;
            justify-content: end;
    
}

.nav-links:hover, .nav-links:focus {
    border-bottom: 2px solid #fff;
}

.nav-links {
    color: #fff;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    text-decoration: none;
    padding: 0.5rem 2rem;
    height: 100%;
    font-size: 18px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    line-height: 21.94x;
}

.fa-bars {
    color: #fff;
}

.menu-icon {
    display: none;
}

@media screen and (max-width: 960px) {
    .nav-menu {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        width: 100%;
        height: 100vh;
        position: absolute;
        top: 80px;
        left: -100%;
        transition: all 0.5s ease;
    }

    .nav-menu.active {
        background: #181719;
        left: 0;
        opacity: 1;
        transition: all 0.6s ease;
        z-index: 1;
    }

    .nav-links {
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
    }

    .nav-links:hover {
        border-bottom: 2px solid #fff;
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        transition: all 0.3s ease;
    }

    .nav-item {
        width: 100%;
    }

    .navbar-logo {
        position: absolute;
        top: 0;
        left: 0;
        -webkit-transform: translate(25%, 50%);
                transform: translate(25%, 50%);
      }

    .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        -webkit-transform: translate(-100%, 60%);
                transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
    }
    
    .fa-times {
        color: #fff;
        font-size: 2rem;
    }
}


* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'PT Sans', sans-serif;
}

body {
  background: #181719;
  color: #fff;
}
.container {
    max-width: 1300px;
}

.row {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.hero_content {
    padding: 100px 0;
    width: 90%;
}

img {
    max-width: 100%;
}

.text_main {
    -webkit-justify-content: center;
