}

.cc {
    font-size: 12px;
    color: #fff;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: 100px;
    margin-bottom: 20px;
}


@media screen and (max-width: 600px) {
    .row {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
    }

    .hero_content {
        padding: 50px 0;
        width: 90%;
    }

    .main_t {
        font-size: 36px;
        line-height: 46.08px;
    }

    .main_p {
        font-size: 14px;
        line-height: 17.07px;
    }

    .read_more {
        font-size: 14px;
    }

    .main_img {
        width: 100%;
        margin-top: 50px;
        margin-left: auto;
        margin-right: auto;
    }

    .tweet_card {
        width: 90%;
        margin-left: auto;
        margin-right: auto;
    }
}
